#modal-root {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 100;
}

a {
  text-decoration: none;
  color: #8585AD;
}

a:hover {
  color:#F2F2F3;
}

.container {
  max-width: 1280px;
  margin: auto;
  box-sizing: border-box;
}

.main__container {
  display: flex;
  justify-content: space-between;
}